import mInput from '../../components/m-input.vue';
export default {
	components: {
		mInput
	},
	data() {
		return {
			providerList: [],
			hasProvider: false,
			account: '',
			password: '',
			positionTop: 0,
			loginType: true
		};
	},

	methods: {
		initPosition() {
			/**
			 * 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
			 * 反向使用 top 进行定位，可以避免此问题。
			 */
			this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
		},
		bindLogin() {
			// if (this.account.length < 5) {
			// 	uni.showToast({
			// 		icon: 'none',
			// 		title: '账号最短为 5 个字符'
			// 	});
			// 	return;
			// }
			// if (this.password.length < 6) {
			// 	uni.showToast({
			// 		icon: 'none',
			// 		title: '密码最短为 6 个字符'
			// 	});
			// 	return;
			// }
			uni.navigateTo({
				url: '../index/index'
			})
		},
		changeLoginType() {
			this.loginType = !this.loginType;
		}
	},
	onReady() {
		this.initPosition();
	}
};
